.loader {
  text-align: center;
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  margin: 20px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  text-align: center;
  color: red;
  margin: 20px;
  display: none;
}

.cat-info {
  margin: 20px;
  display: none;
}

.cat-info img {
  width: 200px;
  height: auto;
  margin-bottom: 10px;
}

.cat-details {
  margin-top: 10px;
}

